import { Component, ViewChild, Input } from '@angular/core';
import { Range, Item } from 'ionic-angular';
import { HomePage } from '../../pages/home/home';

/**
 * Generated class for the FloatRangeComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'float-range',
  //templateUrl: 'float-range.html',
  template: '<ion-item no-lines><ion-range #range [min]="min" [max]="max" [pin]="pin" [step]="step" [snaps]="snaps" [(ngModel)]="_ngModel"  ngDefaultControl><ion-label range-left>20</ion-label><ion-label range-right>50</ion-label></ion-range></ion-item>'
})
export class FloatRangeComponent {

  //text: string;
  @Input() max: number;
  @Input() min: number;
  @Input() pin: boolean;
  @Input() step: number;
  @Input() snaps: boolean;
  @Input() _ngModel: any;
  //@Input('formData.waist') formData: HomePage;
  //@Input() ngModel: any;

  //constructor() {
  //  console.log('Hello FloatRangeComponent Component');
  //  this.text = 'Hello World';
  //}

  @ViewChild('range')
  set range(range: Range) {
    let floatRange: FloatRangeComponent = this;
    range._ratioToValue = function(ratio: number) {
      this._step = Math.round(floatRange.step * 100) / 100;
      ratio = (this._max - this._min) * ratio;
      ratio = (ratio / this._step) * this._step + this._min;
      return Math.round(ratio * ( 1 / this._step)) / ( 1 / this._step);
    }
  }

  onValueChange(event){console.log("changed");}
}



